import { h, Component } from 'preact';
import { connect } from 'preact-redux';
import Modal from '../preact-modal/preact-modal';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
    };
  }

  close() {
    this.setState({
      isOpen: false,
    });
  }

  open() {
    this.setState({
      isOpen: open,
    });
  }

  render() {
    return (
      <div id="page-index">
        <div className="content text-center">
          <h2 className="text-center">Preact Modal</h2>
          <p className="slogan">Just a Preact modal component</p>
          <br />
          <button type="button" onClick={() => this.open()} >
            open
          </button>
        </div>

        <Modal
          isOpen={this.state.isOpen}
        >
          <div className="inner" style="text-align: center;">
            <div className="dialog-close" onClick={() => this.close()}></div>
            <img alt="logo" src="http://forsigner.com/images/omelette-logo.jpg" style={{ width: '50%' }} />
            <br/>
            <div className="wrap-btn">
              <button className="ok" onClick={() => this.close()}>ok</button>
              <button className="close" onClick={() => this.close()}>close</button>
            </div>
          </div>
        </Modal>
      </div>
    );

  }
}



